.call-to-action-container {
  width: 73.75rem;
  height: 11.188rem;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 5.938rem;
  /* animation: 0.5s hello linear; */
}

@keyframes hello {
  0% {
    opacity: 0;
    margin-top: 8rem;
  }
  50% {
    opacity: 0.4;
    margin-top: 8rem;
  }
  100% {
    opacity: 1;
    margin-top: 2.938rem;
  }
}

.arrow {
  height: 25px;
  margin-left: 1rem;
}

.lets-talk-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  cursor: pointer;
}

.lets-talk-text {
  font-family: HelveticaNeue-Medium;
  font-size: 0.688rem;
  line-height: 1.25rem;
  letter-spacing: 1px;
}

.cta-text {
  font-family: HelveticaNeue-Bold;
  font-size: 1.5rem;
  line-height: 2rem;
  letter-spacing: -0.63px;
}
